import React, {Component, useState} from 'react';
import './app-not-found-page.scss';
import {NavLink} from 'react-router-dom';
import {Navigate} from 'react-router-dom';
import {Link} from 'react-router-dom';
import {useAuth} from 'hooks/use-auth';
import {useDispatch} from "react-redux";
import {removeUser} from "store/slices/userSlice";
import IconButton from '@mui/material/IconButton';
import EmojiObjectsIcon from '@mui/icons-material/EmojiObjects';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import AccountCircle from '@mui/icons-material/AccountCircle';


const AppNotfoundpage = ({onMenuOff}) => {

    const dispatch = useDispatch();
    const {isAuth, email} = useAuth();


    const handleClick = () => {
        console.info('You clicked the Chip.');
    };

    const [auth, setAuth] = React.useState(true);
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleChange = (event) => {
        setAuth(event.target.checked);
    };

    const handleMenu = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };


    return isAuth ? (


        <>

            <div className="app__menu-top">
                <div className="app__menu-top_right">

                    <div className="app__menu-top_right-description">
                    <Link to="/"><EmojiObjectsIcon fontSize="large"/></Link>
                    <Link to="/"><div>   Dialux Evo</div></Link>
                    </div>

                </div>
                <div className="app__menu-top_left">
                   
                    <IconButton

                        size="large"
                        aria-label="account of current user"
                        aria-controls="menu-appbar"
                        aria-haspopup="true"
                        onClick={handleMenu}
                        color="inherit"
                    >
                        <AccountCircle/>
                    </IconButton>
                    <Menu
                        id="menu-appbar"
                        anchorEl={anchorEl}
                        anchorOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        keepMounted
                        transformOrigin={{
                            vertical: 'top',
                            horizontal: 'right',
                        }}
                        open={Boolean(anchorEl)}
                        onClose={handleClose}
                    >

                        <MenuItem onClick={() => dispatch(removeUser())}></MenuItem>
                    </Menu>
                </div>


            </div>



            <div class='app__home-header'>

                <div class="app__home-header_top">


                    <div class="app__home-header_top-container">

                        <div class="app__home-header_top-container-title">
                            <h1 class='work'> 404.    ,   !</h1>
                        </div>



                    </div>
                    <div className="app__home-title">

                        <h3 className='app__home-title-h3'>    <NavLink to={"/"}><u> </u></NavLink></h3>

                    </div>



                </div>

            </div>

        </>

    ) : (
        <Navigate to="/login" />
    )


}

export default AppNotfoundpage;